function Word({ children, ...props }) {
  const color = new THREE.Color()
  const fontProps = { font: 'https://cdnjs.cloudflare.com/ajax/libs/mdbootstrap/4.11.0/font/roboto/Roboto-Regular.woff', fontSize: 0.6, letterSpacing: -0.05, lineHeight: 1, 'material-toneMapped': false }
  const ref = useRef()
  const [hovered, setHovered] = useState(false)
  const over = (e) => (e.stopPropagation(), setHovered(true))
  const out = () => setHovered(false)
  
  useEffect(() => {
    if (hovered) document.body.style.cursor = 'pointer'
    return () => (document.body.style.cursor = 'auto')
  }, [hovered])

  useFrame(({ camera }) => {
    ref.current.quaternion.copy(camera.quaternion) // Make text face the camera
    ref.current.material.color.lerp(color.set(hovered ? 'yellow' : '#0971f1'), 0.1)
  })
  return <Text ref={ref} onPointerOver={over} onPointerOut={out} onClick={() => console.log('clicked')} {...props} {...fontProps} children={children} />
}

function Words3d({ maxCount = 10, radius = 20, ...props }) {
  const words = useMemo(() => {
    const temp = []
    const spherical = new THREE.Spherical()
    const count = randomWords.length > maxCount ? maxCount : randomWords.length;
    const phiSpan = Math.PI / (count + 1)
    const thetaSpan = (Math.PI * 2) / count
    for (let i = 1; i < count + 1; i++)
      for (let j = 0; j < count; j++) temp.push([new THREE.Vector3().setFromSpherical(spherical.set(radius, phiSpan * i, thetaSpan * j)), randomWords[i+j]])
    return temp
  }, [maxCount, radius])
  return words.map(([pos, word], index) => <Word key={index} position={pos} children={word} />)
}

////////////////////////////////////

function Robot(props) {
  console.log('Robot()');

  const { scene, animations } = useGLTF('https://threejs.org/examples/models/gltf/RobotExpressive/RobotExpressive.glb')
  const { actions } = useAnimations(animations, scene)
  useEffect(() => {
    actions.Idle.play()

    /*
    // https://threejs.org/docs/scenes/material-browser.html#MeshToonMaterial
    map: textureLoader.load( '../../examples/textures/brick_diffuse.jpg' ), // bricks
    gradientMap: textureLoader.load( '../../examples/textures/gradientMaps/fiveTone.jpg' ), // fiveTone
    alphaMap: alphaMapKeys[ 0 ]
    */
    const bricksL = new THREE.TextureLoader()
    const bricks = bricksL.load( 'three/examples/jsm/textures/brick_diffuse.jpg' );
    bricks.wrapS = THREE.RepeatWrapping;
    bricks.wrapT = THREE.RepeatWrapping;
    bricks.repeat.set( 9, 1 ); 

    const fiveToneL = new THREE.TextureLoader()
    const fiveTone = fiveToneL.load( 'three/examples/jsm/textures/gradientMaps/fiveTone.jpg' );
    fiveTone.minFilter = THREE.NearestFilter;
    fiveTone.magFilter = THREE.NearestFilter;

    // const transparent = true;
    // const opacity = 1;
    // const gradientMap = fiveTone;
    // const color = 0x0000ff;

    // var newMaterial = new THREE.MeshToonMaterial( { gradientMap, color, transparent, opacity, thickness: 0.05} );
    // newMaterial.userData.outlineParameters = {
    //   thickness: 0.05,
    //   color: [ 0, 0, 0 ],
    //   alpha: 0.1,
    //   visible: true,
    //   keepAlive: true,
    // };
 
    const newMaterial = new THREE.MeshToonMaterial( {
      color: 'yellow', //bricksL,
      //gradientMap: fiveToneL,
      transparent:0.5,
      opacity:0.8
    });

    newMaterial.userData.outlineParameters = {
      thickness: 0.05,
      color: [ 0, 0, 0 ],
      alpha: 0.1,
      visible: true,
      keepAlive: true,
    };

    scene.traverse((obj) =>{
      obj.isMesh && (obj.receiveShadow = obj.castShadow = true)
      //if (obj.isMesh) obj.material = newMaterial;
    })

  }, [actions, scene])

  useFrame(({ camera, scene, gl }) => {

    //gl.autoClear = true
    const effect = new OutlineEffect( gl, {
      defaultThickness: 0.05,
      defaultColor: [ 0, 0, 0 ],
      defaultAlpha: 0.8,
      defaultKeepAlive: true // keeps outline material in cache even if material is removed from scene
    } );
    effect.autoClear = true
    //gl.clearDepth();
    effect.render( scene, camera );
  })
  
  return <primitive object={scene} {...props} />
}

////////////////////////////////////

function ShakeCamera() {
  console.log('ShakeCamera()');

  const shakeRef = useRef();
  const orbitRef = useRef();
  useEffect(() => {
    orbitRef.current.addEventListener("change", () => {
      const shake = shakeRef.current.getIntensity();
      shakeRef.current.setIntensity(shake + 0.015);
    });
  }, [orbitRef, shakeRef]);
  return (
    <>
      <OrbitControls ref={orbitRef} makeDefault/>
      <CameraShake ref={shakeRef} additive decay />
    </>
  );
}

////////////////////////////////////

function MoveCam() {
  console.log('MoveCam()');
  const camera = useThree((state) => state.camera)
  return useFrame((state) => {
    camera.position.y = 10 + Math.sin(state.clock.elapsedTime) * 5
  })
}
