class App extends React.Component {

  state = {
    greeted: false,
    name: 'Ruben'
  };

  componentDidMount() {
    this.setState({greeted: true});
  }

  shouldComponentUpdate() {
    console.log('Juju homr - shouldComponentUpdate');
    return false;
  }

  render() {
    return <Canvas shadows camera={{ position: [0, 10, 0], fov: 55 }}>
      <Suspense fallback={null}>
        <ambientLight intensity={0.8} />
        <pointLight intensity={2} position={[0, 0, -1000]} />
        {/* <Sky azimuth={0.1} turbidity={10} rayleigh={0.5} inclination={0.6} distance={1000} /> */}
        <Environment>
          <Lightformer
            form="circle" // circle | ring | rect (optional, default = rect)
            intensity={1} // power level (optional = 1)
            color="yellow" // (optional = white)
            scale={[100, 100]} // Scale it any way you prefer (optional = [1, 1])
            target={[0, 0, 0]} // Target position (optional = undefined)
          />
        </Environment>
        <Grass />
        <Cloud position={[-4, 12, -5]} speed={0.2} opacity={1} />
        <Cloud position={[4, 22, -5]} speed={0.2} opacity={0.5} />
        <Cloud position={[-4, 18, -10]} speed={0.2} opacity={1} />
        <Cloud position={[4, 26, 5]} speed={0.2} opacity={0.5} />
        <Cloud position={[4, 14, 8]} speed={0.2} opacity={0.75} />
        <mesh position={[0, 14, 0]}>
          <Words3d maxCount={10} radius={14} />{/* <TrackballControls /> */}
        </mesh>
        <Stage contactShadow={{ opacity: 1, blur: 2 }}>
          <Robot position={[0, 0, 0]} />
          <ContactShadows position={[0, 0, 0]} opacity={0.75} scale={10} blur={2.5} far={4} />
        </Stage>
        {/* <mesh>
          <Html scale={100} rotation={[Math.PI / 2, 0, 0]} position={[0, 0, 0.51]}>
              <h1>Hello World !</h1>
          </Html>
        </mesh> */}
        {/* <MoveCam />
        <ShakeCamera /> */}
        <OrbitControls makeDefault />
      </Suspense>
    </Canvas>
  }
}

export default App;